import utils from 'ntils';

export default function Scope(parent, props) {
  // scope  _observer_ 
  // scope 
  // utils.cteate  _observer_ 
  // scope  defineProperty  parentScope
  let scope = utils.create(parent);
  utils.copy(props, scope);
  // func  scope ;
  utils.each(parent, (key, value) => {
    if (!utils.isFunction(value)) return;
    scope[key] = value.bind(parent);
  });
  return scope;
}


