import utils from 'ntils';

/**
 * 
 */
export default class Expression {

  /**
   * 
   * @param {string} code 
   * @param {boolean} mix 
   * @returns {void} 
   */
  constructor(code, mix) {
    this.func = mix ?
      this._compileMixedCode(code) :
      this._compileCode(code);
  }

  /**
   * 
   * @param {string} code 
   * @returns {function} 
   */
  _compileCode(code) {
    code = this._escapeEOL(this._wrapCode(code));
    return this._createFunction(code);
  }

  /**
   * 
   * @param {string} code 
   * @returns {function} 
   */
  _compileMixedCode(code) {
    let statements = this._parseMixedCode(code);
    code = this._escapeEOL(statements.join('+'));
    return this._createFunction(code);
  }

  /**
   * 
   * @param {string} code 
   * @returns {function} 
   */
  _createFunction(code) {
    let func = new Function('utils', 'scope',
      'with(scope){return ' + code + '}');
    return func.bind(null, utils);
  }

  /**
   * 
   * @param {string} code 
   * @returns {Array} token
   */
  _parseMixedCode(code) {
    let index = 0, length = code.length;
    let token = '', isExpr = false, tokens = [];
    while (index <= length) {
      let char = code[index++];
      let nextChar = code[index];
      if (utils.isNull(char)) {
        if (token.length > 0) {
          tokens.push('"' + this._escapeCode(token) + '"');
        }
        token = '';
        isExpr = false;
      } else if (!isExpr && char + nextChar == '{{') {
        if (token.length > 0) {
          tokens.push('"' + this._escapeCode(token) + '"');
        }
        token = '';
        isExpr = true;
        index++;
      } else if (isExpr && char + nextChar == '}}') {
        if (token.length > 0) {
          tokens.push(this._wrapCode(token));
        }
        token = '';
        isExpr = false;
        index++;
      } else {
        token += char;
      }
    }
    return tokens;
  }

  /**
   * 
   * @param {string} code 
   * @returns {string} 
   */
  _escapeCode(code) {
    return code.replace(/"/, '\\"')
      .replace('\r\n', '\\r\\n')
      .replace('\n', '\\n');
  }

  /**
   * 
   * @param {string} code 
   * @returns {string} 
   */
  _escapeEOL(code) {
    return code.replace(/\n/gm, '\\\n');
  }

  /**
   *  try/cache 
   * 
   * @param {string} code 
   * @returns {string} 
   */
  _wrapCode(code) {
    return `((function(){try{return ${code}}catch(err){console.error(err);return err}})())`;
  }

  /**
   *  scope 
   * @param {Object} scope 
   * @returns {Object} 
   */
  execute(scope) {
    if (utils.isNull(scope)) {
      scope = {};
    }
    return this.func.call(scope, scope);
  }

}


